.contactBox {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5vh;
  width: 100vw;
  height: 100vh;
  transform: translateY(100vh);
  background: white;
  background: linear-gradient(
    #476097,
    transparent,
    white,
    white,
    white,
    white,
    transparent
  );
  transition: 0.75s cubic-bezier(0.4, 0, 0.4, 1);
}

#contactBoxHeader {
  margin: 1rem 0;
}

#contactBoxHeader h2 {
  font-weight: 800;
  font-size: 2rem;
}

#inputsBox {
  display: flex;
  flex-direction: column;
  width: 30%;
}

input {
  background-color: rgb(166, 170, 175);
  border: 0.2rem solid darkslateblue;
  font-family: "Quicksand";
  width: 20vw;
  font-size: 1.5rem;
  padding: 1rem;
  transition: 1s;
}

#nameInput {
  transform: translateX(100vw);
}
#emailInput {
  transform: translateX(-100vw);
}
#phoneInput {
  transform: translateX(100vw);
}

textarea {
  background-color: rgb(166, 170, 175);

}

.submitButton {
  margin-top: 1rem;
  width: 10rem;
  height: 3rem;
  background-color: slategray;
}

.submitButton h2 {
  color: darkblue;
  letter-spacing: .25em;
  margin-top: .5em;
}

.submitButton h2:hover {
  color: chartreuse;
}

.myContactInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transform: translateY(100vh);
  transition: 1s cubic-bezier(0.4, 0, 0.4, 1);
}

.myContactInfo h2 {
  font-weight: 900;
  font-size: 2rem;
}

.myEmail {
  margin: auto auto;
}
